.button {
  border: none;
	background: #414141;
	color: #fff7f7;
	padding: 10px;
	font-size: 18px;
	border-radius: 5px;
	position: relative;
	box-sizing: border-box;
	-webkit-transition: all 500ms ease;
	transition: all 500ms ease;
}


.button:after {
	top: -100%;
	left: 0px;
	width: 100%;
	position: absolute;
	background: #3a9999;
	border-radius: 5px;
	content: 'Flipped';
	-webkit-transform-origin: left bottom;
	        transform-origin: left bottom;
	-webkit-transform: rotateX(90deg);
	        transform: rotateX(90deg);
}

.button:hover {
  width: 100%;
  background: #252525;
  color: #fff7f7;
	box-shadow: inset 0 0 0 3px #414141;
}

.Tag {
    height: 50%;
    width: 100px;
    border-radius: 5px;
    background-color: #2f3640;
    color: #dcdde1;
    margin: 1%;
    font-size: 70%;
    
}

.Tag:hover {
    background-color: #dcdde1;
    color: #2f3640;
}
.AppScreen {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.Card {
    box-shadow: 0 4px 8px 0 #353b48;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 30%;
    margin-bottom: 2%;
    position: relative;
    border-radius: 10px;
}

.Card span {
    visibility: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    color: #f5f6fa;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    line-height: 100%;
    
}

.Card:hover {
    box-shadow: 0 4px 8px 0 #718093;  
}

.Card:hover .AppScreen {
    -webkit-filter: none;
            filter: none;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    opacity: 0.4;
}

.Card:hover span {
    visibility: visible;
    -webkit-transition: all 0.2s linear;
            transition: all 0.2s linear;
}

.Tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: space-between;
    color: #dcdde1;
}

@media screen and (max-width: 600px) {
    .Card {
        width: 100%;
        margin-bottom: 5%; 
    }
}

.Container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

@media screen and (max-width: 600px) {
    .Container {
        flex-direction: column;
    }
}

